import React, { useState } from "react";
import { uploadProductsExcel } from "../../api/api";

type Props = {
  token: string;
  onClose: () => void;
  onSuccess: () => void;
};

const UploadProduct: React.FC<Props> = ({ token, onClose, onSuccess }) => {
  const [file, setFile] = useState<File | null>(null);
  const [uploading, setUploading] = useState(false);
  const [message, setMessage] = useState<string | null>(null);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0] || null;
    setFile(selectedFile);
    setMessage(null);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!file) {
      setMessage(" Please select an Excel file first.");
      return;
    }

    try {
      setUploading(true);
      const res = await uploadProductsExcel(token, file);
      setMessage(` Uploaded successfully! Created ${res.created} products.`);
      onSuccess();
    } catch (err: any) {
      setMessage(` Error: ${err.message}`);
    } finally {
      setUploading(false);
    }
  };

  return (
    <div className="w-full min-h-screen flex items-center justify-center bg-gray-100 px-4 py-10">
      {/* Card */}
      <div className="bg-white w-full max-w-lg rounded-xl shadow-xl p-6 animate-fadeIn">

        {/* Title */}
        <h2 className="text-2xl font-semibold text-gray-800 mb-5 text-center">
           Upload Products (Excel)
        </h2>

        {/* Upload Form */}
        <form onSubmit={handleSubmit} className="space-y-5">

          {/* File Input Area */}
          <label className="flex flex-col items-center justify-center border-2 border-dashed border-gray-300 
                          rounded-lg p-6 hover:bg-gray-50 transition cursor-pointer">
            <input
              type="file"
              accept=".xlsx, .xls"
              className="hidden"
              disabled={uploading}
              onChange={handleFileChange}
            />
            <span className="text-sm text-gray-600">Click to choose Excel file</span>

            {file && (
              <p className="mt-2 text-blue-600 text-sm font-medium">
                 {file.name}
              </p>
            )}
          </label>

          {/* Buttons */}
          <div className="flex gap-3">
            <button
              type="submit"
              disabled={!file || uploading}
              className={`flex-1 py-2 rounded-lg text-white font-medium transition ${
                uploading || !file
                  ? "bg-blue-300 cursor-not-allowed"
                  : "bg-blue-600 hover:bg-blue-700"
              }`}
            >
              {uploading ? "Uploading..." : "Upload"}
            </button>

            <button
              type="button"
              onClick={onClose}
              className="flex-1 py-2 rounded-lg bg-gray-200 hover:bg-gray-300 
                         text-gray-800 transition font-medium"
            >
              Cancel
            </button>
          </div>
        </form>

        {/* Status Message */}
        {message && (
          <p
            className={`mt-4 text-center font-medium ${
              message.startsWith("") ? "text-green-600" : "text-red-600"
            }`}
          >
            {message}
          </p>
        )}

        {/* Instructions */}
        <div className="mt-6 border-t pt-4">
          <p className="text-sm font-semibold text-gray-700 mb-1">
            Expected Excel Columns:
          </p>

          <div className="bg-gray-100 p-3 rounded-lg text-sm text-gray-700">
            name, ref_no, description, category, hsn, discount, brand,
            cas_no, gst, variant_label, est_price, price
          </div>

          <p className="text-sm text-gray-600 mt-2">
            Multiple rows per product allowed using the same{" "}
            <strong>ref_no</strong> for variants.
          </p>

          {/* Download Template */}
          <div className="mt-5 flex justify-center">
            <a
           
              href="/Linkzur_products_upload.xlsx"
              download
              className="px-4 py-2 text-sm bg-green-600 hover:bg-green-700 
                         text-white rounded-lg font-medium transition"
            >
               Download Excel Template
            </a>
          </div>
        </div>
      </div>

      {/* Animation */}
      <style>{`
        @keyframes fadeIn {
          from { opacity: 0; transform: scale(0.97); }
          to { opacity: 1; transform: scale(1); }
        }
        .animate-fadeIn {
          animation: fadeIn 0.25s ease-out;
        }
      `}</style>
    </div>
  );
};

export default UploadProduct;
