import React, { useEffect, useState, JSX } from "react";
import { useNavigate } from "react-router-dom";
import { Heart, X, ShoppingCart, Eye } from "lucide-react";

import Header from "../../components/buyer/Header";
import BannerAd from "../../components/common/Banner";
import RecentlyViewed from "../../components/buyer/RecentlyViewed";
import ProductsList from "../../components/common/ProductsList";
import UserProfile from "../../components/buyer/BuyerProfile";
import ProductDetails from "../../components/buyer/ProductDetails";
import CartPanel from "../../components/buyer/CartPanel";
import WishlistPanel from "../../components/buyer/WishlistPanel";
import NotificationsPanel from "../../components/common/NotificationsPanel";
import OrdersList from "../../components/buyer/OrdersList";
import PaymentPanel from "../../components/buyer/PaymentPanel";
import Chat from "../../components/common/ChatPanel";
import QuotationsPanel from "../../components/buyer/QuotationsPanel";
import About from "../../components/common/website/About";

import {
  getProfile,
  getProducts,
  getCart,
  addToCart,
  clearCartItem,
  removeCartItem,
  getWishlist,
  toggleWishlist,
  searchProducts,
  getNotifications,
  markNotificationRead
} from "../../api/api";

import {
  Product,
  ProductVariant,
  CartItem,
  Notification,
  WishlistItem,
} from "../../types";

import { CATEGORIES } from "../../constants/Lists";


const DEFAULT_IMAGE = "/images/default_product.png";

const BACKEND_URL =
  process.env.NODE_ENV === "production" ? "" : "http://127.0.0.1:8000";

/**  REUSED NORMALIZER (works everywhere) */
const normalizeProducts = (products: Product[]): Product[] =>
  products.map((p) => ({
    ...p,
    image: p.image
      ? p.image.startsWith("http")
        ? p.image
        : `${BACKEND_URL}${p.image}`
      : DEFAULT_IMAGE,
    variants: p.variants || [],
  }));

/**  SAME LOGIC BUT FOR CART ITEMS */
const normalizeCart = (items: CartItem[]): CartItem[] =>
  items.map((item) => ({
    ...item,
    product: {
      ...item.product,
      image: item.product.image
        ? item.product.image.startsWith("http")
          ? item.product.image
          : `${BACKEND_URL}${item.product.image}`
        : DEFAULT_IMAGE,
    },
  }));

export default function BuyerHome() {
  const navigate = useNavigate();

  const [token, setToken] = useState<string | null>(null);
  const [userEmail, setUserEmail] = useState<string>("");

  const [products, setProducts] = useState<Product[]>([]);
  const [searchResults, setSearchResults] = useState<Product[]>([]);
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [loading, setLoading] = useState(true);
  const [searching, setSearching] = useState(false);

  const [cartItems, setCartItems] = useState<CartItem[]>([]);
  const [wishlist, setWishlist] = useState<Product[]>([]);
  const [notifications, setNotifications] = useState<Notification[]>([]);

  const [activePanel, setActivePanel] = useState<
    | "home"
    | "cart"
    | "wishlist"
    | "orders"
    | "notifications"
    | "payment"
    | "chat"
    | "profile"
    | "quotations"
    | "about"
  >("home");

  const [conversationId, setConversationId] = useState<number | null>(null);

  // ---------------------- Initial Load ----------------------
  useEffect(() => {
    const init = async () => {
      try {
        const storedToken = localStorage.getItem("access_token");
        setToken(storedToken);

        if (!storedToken) {
          setLoading(false);
          return;
        }

        const profile = await getProfile(storedToken);
        setUserEmail(profile.email || "");

        const productData = await getProducts(storedToken);
        setProducts(normalizeProducts(productData));

        const cartData = await getCart(storedToken);
        setCartItems(normalizeCart(cartData));

        const wishlistData = await getWishlist(storedToken);
        setWishlist(
          normalizeProducts(wishlistData.map((w: WishlistItem) => w.product))
        );

        const notifs = await getNotifications(storedToken);
        setNotifications(notifs);

      } catch (err) {
        console.error(err);
        localStorage.clear();
      } finally {
        setLoading(false);
      }
    };

    init();
  }, []);

  // ---------------------- CART ----------------------
  const refreshCart = async () => {
    if (!token) return;
    const cartData = await getCart(token);
    setCartItems(normalizeCart(cartData));
  };

  const handleAddToCart = async (
    product: Product,
    quantity: number,
    variant?: ProductVariant
  ) => {
    if (!token) return alert("Please login to add items to cart.");

    try {
      await addToCart(token, product.id, quantity, variant?.id ?? null);
      await refreshCart();
    } catch {
      alert("Could not update cart.");
    }
  };

  // ---------------------- WISHLIST ----------------------
  const handleToggleWishlist = async (product: Product) => {
    if (!token) return alert("Please login to manage your wishlist.");

    const exists = wishlist.some((p) => p.id === product.id);
    await toggleWishlist(token, product.id.toString(), exists);

    const updated = await getWishlist(token);
    setWishlist(
      normalizeProducts(updated.map((w: WishlistItem) => w.product))
    );
  };

  // ---------------------- SEARCH ----------------------
  const handleSearch = async (query: string) => {
    if (!query) {
      setSearching(false);
      setSearchResults([]);
      setActivePanel("home");
      return;
    }

    const categoryMatch = CATEGORIES.find(
      (c) => c.key.toLowerCase() === query.toLowerCase()
    );

    if (categoryMatch) {
      const data = await getProducts(token!, categoryMatch.key);
      setSearchResults(normalizeProducts(data));
      setSearching(true);
      return;
    }

    const data = await searchProducts(query);
    setSearchResults(normalizeProducts(data));
    setSearching(true);
  };

  // ---------------------- MAIN CONTENT ----------------------
  let bodyContent: JSX.Element | null = null;

  if (selectedProduct) {
    bodyContent = (
      <ProductDetails
        product={selectedProduct}
        onBack={() => setSelectedProduct(null)}
        onAddToCart={handleAddToCart}
        token={token!}
      />
    );
  } else if (activePanel === "cart") {
    bodyContent = (
      <CartPanel
        products={cartItems}
        onClose={() => setActivePanel("home")}
        onCheckout={() => setActivePanel("payment")}
        onUpdateQuantity={async (cartItemId, productId, qty) => {
          const current = cartItems.find((c) => c.id === cartItemId);
          const variantId = current?.variant?.id ?? null;

          if (qty <= 0) {
            await removeCartItem(token!, cartItemId);
          } else {
            await addToCart(token!, productId, qty, variantId);
          }

          await refreshCart();
        }}
        onClearItem={async (cartItemId) => {
          await clearCartItem(token!, cartItemId);
          await refreshCart();
        }}
      />
    );
  } else if (activePanel === "wishlist") {
    bodyContent = (
      <WishlistPanel
        products={wishlist}
        onClose={() => setActivePanel("home")}
        onAddToCart={handleAddToCart}
        onToggleWishlist={handleToggleWishlist}
        onSelectProduct={setSelectedProduct}
      />
    );
  } else if (activePanel === "orders") {
    bodyContent = (
      <OrdersList
        token={token!}
        onClose={() => setActivePanel("home")}
        onStartChat={(cid) => {
          setConversationId(cid ?? null);
          setActivePanel("chat");
        }}
      />
    );
  } else if (activePanel === "notifications") {
    bodyContent = (
      <NotificationsPanel
        notifications={notifications}
        title="Your Notifications"
        onClose={() => setActivePanel("home")}
        onMarkRead={async (id) => {
          await markNotificationRead(token!, id);
          const updated = await getNotifications(token!);
          setNotifications(updated);
        }}
      />
    );
  } else if (activePanel === "payment") {
    bodyContent = (
      <PaymentPanel
        token={token!}
        cartItems={cartItems}
        onClose={() => setActivePanel("home")}
        onOrderPlaced={() => {
          setCartItems([]);
          setActivePanel("orders");
        }}
      />
    );
  } else if (activePanel === "chat") {
    bodyContent = <Chat token={token!} userEmail={userEmail} onClose={() => setActivePanel("home")} />;
  } else if (activePanel === "profile") {
    bodyContent = (
      <UserProfile token={token!} onClose={() => setActivePanel("home")} />
    );
  } else if (activePanel === "quotations") {
    bodyContent = <QuotationsPanel token={token!} onClose={() => setActivePanel("home")} />;
  } else if (activePanel === "about") {
    bodyContent = <About />;

  /* ---------------------------------------------------------
      UPDATED SEARCH BLOCK WITH TOP-RIGHT CLOSE BUTTON
     --------------------------------------------------------- */
  } else if (searching) {
    bodyContent = (
      <div className="relative w-full">

        {/*  Close button always visible in top-right */}
        <button
          onClick={() => {
            setSearching(false);
            setSearchResults([]);
            setActivePanel("home");
          }}
          className="
            absolute top-3 right-1 z-20
            text-gray-600 hover:text-black
            text-2xl font-bold
          "
        >
        <X size={26} className="text-gray-700" />
        </button>

        {searchResults.length === 0 ? (
          <div className="flex flex-col items-center justify-center py-20 text-center">
            <h2 className="text-lg font-semibold text-gray-600 mt-4">
              No products found
            </h2>
            <p className="text-sm text-gray-500">
              Try another search or category.
            </p>
          </div>
        ) : (
          <ProductsList
            title="Search Results"
            products={searchResults}
            onSelectProduct={setSelectedProduct}
            onToggleWishlist={handleToggleWishlist}
            wishlist={wishlist}
          />
        )}
      </div>
    );

  } else {
    bodyContent = (
      <div className="w-[90%] max-w-[1440px] mx-auto mb-10 flex flex-col gap-6">
        <div
          className="
            w-full h-[420px]
            rounded-xl overflow-hidden flex items-center justify-center
            bg-[#f5f5f5] shadow-md
            lg:h-[360px] md:h-[300px] sm:h-[240px] xs:h-[200px]
          "
        >
          <BannerAd />
        </div>

        <RecentlyViewed
          token={token!}
          onSelectProduct={setSelectedProduct}
          onToggleWishlist={handleToggleWishlist}
          wishlist={wishlist}
        />


        <ProductsList
          title="Recommended for You"
          products={products.slice(10, 20)}
          onSelectProduct={setSelectedProduct}
          onToggleWishlist={handleToggleWishlist}
          wishlist={wishlist}
        />
      </div>
    );
  }

  // ---------------------- LOADING ----------------------
  if (loading)
    return (
      <div className="flex items-center justify-center h-screen">
        Loading...
      </div>
    );

  // ---------------------- RETURN ----------------------
  return (
    <div className="flex flex-col w-full min-h-screen bg-[#fafafa]">
      <Header
        token={token}
        onUserPress={() => {
          setSelectedProduct(null);
          setActivePanel("profile");
        }}
        onCartPress={() => {
          setSelectedProduct(null);
          token ? setActivePanel("cart") : navigate("/login");
        }}
        onSearch={handleSearch}
        cartCount={cartItems.reduce((sum, item) => sum + item.quantity, 0)}
        onOrdersPress={() => setActivePanel("orders")}
        onWishlistPress={() => setActivePanel("wishlist")}
        onChatPress={() => setActivePanel("chat")}
        onAboutPress={() => setActivePanel("about")}
        onQuotationsPress={() => setActivePanel("quotations")}
        onNotificationsPress={() => setActivePanel("notifications")}
        notificationCount={notifications.filter((n) => !n.is_read).length}
        onLogout={() => {
          localStorage.clear();
          setToken(null);
          setSelectedProduct(null);
          setActivePanel("home");
        }}
      />

      <div className="flex-1 w-full flex flex-col items-center">
        {bodyContent}
      </div>
    </div>
  );
}

