import React, { useEffect, useState, useRef } from "react";
import { CartItem } from "../../types";
import { placeOrder, getProfile, updateProfile } from "../../api/api";

type Props = {
  token: string;
  cartItems: CartItem[];
  onClose: () => void;
  onOrderPlaced?: () => void;
};

type OrderResponse = { id: string };

type ShippingAddress = {
  id?: number;
  name: string;
  phone: string;
  addressLine1: string;
  addressLine2?: string;
  city: string;
  state: string;
  pincode: string;
  isDefault: boolean;
};

type BillingAddress = {
  name: string;
  phone: string;
  addressLine1: string;
  addressLine2?: string;
  city: string;
  state: string;
  pincode: string;
};

// =====================================
// PRICE HELPER (discount + GST)
// Matches CartPanel logic conceptually
// =====================================
const calculateFinalPrice = (item: CartItem) => {
  const variant = item.variant ?? null;

  // Base price (you can add product.price fallback if needed)
  const basePrice = Number(variant?.price ?? variant?.est_price ?? 0);

  const discount = Number(item.product.discount ?? 0);
  const gst = Number(item.product.gst ?? 0);

  // 1. Discounted price
  const discountedPrice = basePrice - (basePrice * discount) / 100;

  // 2. GST on discounted price
  const gstAmount = (discountedPrice * gst) / 100;

  // 3. Final (discounted + GST)
  const finalPrice = discountedPrice + gstAmount;

  return finalPrice;
};

export default function PaymentPanel({
  token,
  cartItems,
  onClose,
  onOrderPlaced,
}: Props) {
  const scrollRef = useRef<HTMLDivElement>(null);

  const [loading, setLoading] = useState(false);

  // USER PROFILE ADDRESSES
  const [shippingList, setShippingList] = useState<ShippingAddress[]>([]);
  const [billing, setBilling] = useState<BillingAddress | null>(null);

  // SELECTED ADDRESS
  const [selectedShippingId, setSelectedShippingId] = useState<number | null>(
    null
  );

  // Add forms visibility
  const [addingShipping, setAddingShipping] = useState(false);
  const [addingBilling, setAddingBilling] = useState(false);

  // SHIPPING FORM
  const [shippingForm, setShippingForm] = useState<ShippingAddress>({
    name: "",
    phone: "",
    addressLine1: "",
    addressLine2: "",
    city: "",
    state: "",
    pincode: "",
    isDefault: false,
  });

  // BILLING FORM
  const [billingForm, setBillingForm] = useState<BillingAddress>({
    name: "",
    phone: "",
    addressLine1: "",
    addressLine2: "",
    city: "",
    state: "",
    pincode: "",
  });

  // LOAD PROFILE DATA
  useEffect(() => {
    async function load() {
      const profile = await getProfile(token);

      setShippingList(profile.shippingAddresses || []);
      setBilling(profile.billingAddress || null);

      const defaultShip = (profile.shippingAddresses || []).find(
        (a: ShippingAddress) => a.isDefault
      );
      setSelectedShippingId(defaultShip?.id || null);
    }
    load();
  }, [token]);

  // SCROLL TO TOP WHEN FORM OPENS
  useEffect(() => {
    if (addingShipping || addingBilling) {
      scrollRef.current?.scrollTo({ top: 0, behavior: "smooth" });
    }
  }, [addingShipping, addingBilling]);

  // =====================================
  // TOTAL (Final price with GST)
  // =====================================
  const total = cartItems.reduce((sum, item) => {
    const finalPrice = calculateFinalPrice(item);
    return sum + finalPrice * item.quantity;
  }, 0);

  // SAVE SHIPPING ADDRESS TO PROFILE
  const saveNewShipping = async () => {
    if (!shippingForm.name || !shippingForm.addressLine1 || !shippingForm.city) {
      return alert("Please fill all required fields");
    }

    const updatedShipping = [...shippingList, { ...shippingForm, id: Date.now() }];

    // Always send both shipping + billing to avoid wiping one of them
    await updateProfile(token, {
      shippingAddresses: updatedShipping,
      billingAddress: billing ?? billingForm,
    });

    setShippingList(updatedShipping);
    setAddingShipping(false);
    setSelectedShippingId(updatedShipping[updatedShipping.length - 1].id!);
  };

  // SAVE BILLING ADDRESS
  const saveBilling = async () => {
    if (!billingForm.name || !billingForm.addressLine1 || !billingForm.city) {
      return alert("Please complete billing address");
    }

    await updateProfile(token, {
      shippingAddresses: shippingList,
      billingAddress: billingForm,
    });

    setBilling(billingForm);
    setAddingBilling(false);
  };

  // PLACE ORDER
  const handleInvoice = async () => {
    if (!selectedShippingId && shippingList.length > 0)
      return alert("Please select a shipping address");

    if (shippingList.length === 0 && !shippingForm.addressLine1)
      return alert("Please add a shipping address");

    if (!billing && !billingForm.addressLine1)
      return alert("Please add a billing address");

    let shippingAddressStr = "";
    let billingAddressStr = "";

    // SHIPPING
    if (shippingList.length > 0) {
      const selected = shippingList.find((s) => s.id === selectedShippingId);
      shippingAddressStr = `${selected!.name}, ${selected!.addressLine1}, ${selected!.city}, ${selected!.state} - ${selected!.pincode}`;
    } else {
      shippingAddressStr = `${shippingForm.name}, ${shippingForm.addressLine1}, ${shippingForm.city}, ${shippingForm.state} - ${shippingForm.pincode}`;
    }

    // BILLING
    if (billing) {
      billingAddressStr = `${billing.name}, ${billing.addressLine1}, ${billing.city}, ${billing.state} - ${billing.pincode}`;
    } else {
      billingAddressStr = `${billingForm.name}, ${billingForm.addressLine1}, ${billingForm.city}, ${billingForm.state} - ${billingForm.pincode}`;
    }

    const finalAddress = `Shipping: ${shippingAddressStr} | Billing: ${billingAddressStr}`;

    setLoading(true);

    try {
      const items = cartItems.map((ci) => {
        const finalPrice = calculateFinalPrice(ci); // GST included

        return {
          product_id: String(ci.product.id),
          variant_id: ci.variant ? String(ci.variant.id) : undefined,
          quantity: ci.quantity,
          price: finalPrice, // GST-inclusive unit price
        };
      });

      await placeOrder(token, items, finalAddress);

      alert("Order placed successfully!");
      onOrderPlaced?.();
      onClose();
    } catch (err) {
      console.error(err);
      alert("Failed to place order.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="fixed inset-0 bg-black/40 flex items-end justify-center z-50">
      <div className="w-full max-w-lg bg-white rounded-t-2xl shadow-xl flex flex-col h-[90vh]">
        {/* HEADER (fixed) */}
        <div className="px-6 py-4 border-b bg-gray-50 flex justify-between items-center sticky top-0 z-10">
          <h2 className="text-lg font-semibold text-gray-800">Checkout</h2>
          <button onClick={onClose} className="text-gray-500 text-xl">
            
          </button>
        </div>

        {/* SCROLLABLE CONTENT */}
        <div
          ref={scrollRef}
          className="flex-1 overflow-y-auto px-6 py-4 space-y-6"
        >
          {/* SHIPPING AREA */}
          <div>
            <h3 className="font-semibold text-gray-800 mb-2">
              Shipping Address
            </h3>

            {/* No saved shipping */}
            {shippingList.length === 0 && !addingShipping && (
              <button
                onClick={() => setAddingShipping(true)}
                className="text-blue-600 underline text-sm"
              >
                + Add Shipping Address
              </button>
            )}

            {/* Shipping list */}
            {shippingList.length > 0 && !addingShipping && (
              <>
                <select
                  value={selectedShippingId ?? ""}
                  onChange={(e) =>
                    setSelectedShippingId(Number(e.target.value))
                  }
                  className="w-full border px-3 py-2 rounded-lg"
                >
                  <option value="">Select shipping address</option>
                  {shippingList.map((s) => (
                    <option key={s.id} value={s.id!}>
                      {s.name}  {s.addressLine1}
                    </option>
                  ))}
                </select>

                <button
                  onClick={() => setAddingShipping(true)}
                  className="text-blue-600 underline text-sm mt-2"
                >
                  + Add Another Address
                </button>
              </>
            )}

            {/* Add shipping form */}
            {addingShipping && (
              <div className="space-y-2 mt-3 bg-gray-50 p-3 rounded-lg border">
                {[
                  "name",
                  "phone",
                  "addressLine1",
                  "addressLine2",
                  "city",
                  "state",
                  "pincode",
                ].map((field) => (
                  <input
                    key={field}
                    placeholder={field}
                    value={(shippingForm as any)[field] || ""}
                    onChange={(e) =>
                      setShippingForm((prev) => ({
                        ...prev,
                        [field]: e.target.value,
                      }))
                    }
                    className="w-full border rounded px-3 py-2"
                  />
                ))}

                <div className="flex justify-end gap-2">
                  <button
                    className="px-3 py-1 bg-gray-300 rounded"
                    onClick={() => setAddingShipping(false)}
                  >
                    Cancel
                  </button>
                  <button
                    className="px-3 py-1 bg-blue-600 text-white rounded"
                    onClick={saveNewShipping}
                  >
                    Save
                  </button>
                </div>
              </div>
            )}
          </div>

          {/* BILLING AREA */}
          <div>
            <h3 className="font-semibold text-gray-800 mb-2">
              Billing Address
            </h3>

            {/* No billing yet */}
            {!billing && !addingBilling && (
              <button
                onClick={() => setAddingBilling(true)}
                className="text-blue-600 underline text-sm"
              >
                + Add Billing Address
              </button>
            )}

            {/* Existing billing */}
            {billing && !addingBilling && (
              <div className="bg-gray-50 p-3 rounded border">
                <p>{billing.name}</p>
                <p className="text-sm">{billing.addressLine1}</p>
                <p className="text-sm">
                  {billing.city}, {billing.state} - {billing.pincode}
                </p>
                <p className="text-sm"> {billing.phone}</p>

                <button
                  onClick={() => {
                    setBillingForm(billing);
                    setAddingBilling(true);
                  }}
                  className="text-blue-600 underline text-sm mt-2"
                >
                  Edit Billing
                </button>
              </div>
            )}

            {/* Billing form */}
            {addingBilling && (
              <div className="space-y-2 mt-3 bg-gray-50 p-3 rounded-lg border">
                {[
                  "name",
                  "phone",
                  "addressLine1",
                  "addressLine2",
                  "city",
                  "state",
                  "pincode",
                ].map((field) => (
                  <input
                    key={field}
                    placeholder={"Billing " + field}
                    value={(billingForm as any)[field] || ""}
                    onChange={(e) =>
                      setBillingForm((prev) => ({
                        ...prev,
                        [field]: e.target.value,
                      }))
                    }
                    className="w-full border rounded px-3 py-2"
                  />
                ))}

                <div className="flex justify-end gap-2">
                  <button
                    className="px-3 py-1 bg-gray-300 rounded"
                    onClick={() => setAddingBilling(false)}
                  >
                    Cancel
                  </button>
                  <button
                    className="px-3 py-1 bg-green-600 text-white rounded"
                    onClick={saveBilling}
                  >
                    Save Billing
                  </button>
                </div>
              </div>
            )}
          </div>

          {/* CART ITEMS / ORDER SUMMARY */}
          <div>
            <h3 className="font-semibold text-gray-800 mb-2">Order Summary</h3>

            <div className="space-y-3 max-h-56 overflow-y-auto pr-1">
              {cartItems.map((item) => {
                const finalPrice = calculateFinalPrice(item); // includes GST

                return (
                  <div
                    key={item.id || item.product.id}
                    className="flex justify-between items-start border-b pb-2"
                  >
                    <span className="font-medium">{item.product.name}</span>

                    <div className="text-right text-sm">
                      {/* Option A: show only final unit price (GST-included) */}
                      {finalPrice.toFixed(2)}  {item.quantity}
                      <div className="text-xs text-gray-600">
                        = {(finalPrice * item.quantity).toFixed(2)}
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>
          </div>

          {/* TOTAL */}
          <p className="text-right text-lg font-semibold">
            Total: {total.toFixed(2)}
          </p>
        </div>

        {/* FOOTER (fixed) */}
        <div className="px-6 py-4 border-t bg-white sticky bottom-0 flex justify-end gap-3">
          <button onClick={onClose} className="px-4 py-2 border rounded-md">
            Cancel
          </button>

          <button
            onClick={handleInvoice}
            disabled={loading}
            className="px-5 py-2 bg-blue-600 text-white rounded-md"
          >
            {loading ? "Processing" : "Place Order"}
          </button>
        </div>
      </div>
    </div>
  );
}

