import React, { useRef } from "react";
import { Heart, ChevronLeft, ChevronRight } from "lucide-react";
import { Product } from "../../types";

const DEFAULT_IMAGE = "https://via.placeholder.com/180";

type ProductsListProps = {
  title?: string;
  products: Product[];
  onSelectProduct?: (product: Product) => void;
  onToggleWishlist?: (product: Product) => void;
  wishlist?: Product[];
};

const ProductsList: React.FC<ProductsListProps> = ({
  title,
  products,
  onSelectProduct,
  onToggleWishlist,
  wishlist = [],
}) => {
  const scrollRef = useRef<HTMLDivElement>(null);

  const scroll = (dir: "left" | "right") => {
    if (!scrollRef.current) return;
    const scrollAmount = 300;

    scrollRef.current.scrollBy({
      left: dir === "left" ? -scrollAmount : scrollAmount,
      behavior: "smooth",
    });
  };

  return (
    <div className="relative w-full my-6">
      {/* Header */}
      {title && (
        <div className="flex justify-between items-center mb-3 px-2">
          <h2 className="text-xl font-semibold text-gray-900">{title}</h2>
          <a
            href="#"
            className="text-sm text-blue-600 hover:underline font-medium"
          >
            See all
          </a>
        </div>
      )}

      {/* Arrows */}
      <button
        className="absolute left-0 top-1/2 transform -translate-y-1/2 bg-white shadow-md rounded-full p-2 z-10 hover:bg-gray-100"
        onClick={() => scroll("left")}
      >
        <ChevronLeft size={20} />
      </button>

      <button
        className="absolute right-0 top-1/2 transform -translate-y-1/2 bg-white shadow-md rounded-full p-2 z-10 hover:bg-gray-100"
        onClick={() => scroll("right")}
      >
        <ChevronRight size={20} />
      </button>

      {/* Scrollable Product Row */}
      <div
        ref={scrollRef}
        className="flex gap-4 overflow-x-auto scroll-smooth pb-3 px-2 hide-scrollbar"
      >
        {products.length === 0 ? (
          <p className="text-gray-500 text-sm">No items found</p>
        ) : (
          products.map((product) => {
            const isInWishlist = wishlist.some((w) => w.id === product.id);

            const imageSrc =
              typeof product.image === "string" && product.image
                ? product.image
                : DEFAULT_IMAGE;

            // Get price from first variant
            const variant = product.variants?.[0];
            const rawPrice =
              variant?.price ?? variant?.est_price ?? (product as any)?.price ?? 0;

            const price =
              typeof rawPrice === "string"
                ? parseFloat(rawPrice)
                : Number(rawPrice) || 0;

            const discount = Number(product.discount) || 0;
            const finalPrice =
              discount > 0 ? price - price * (discount / 100) : price;

            return (
              <div
                key={product.id}
                className="min-w-[220px] max-w-[220px] bg-white rounded-lg shadow hover:shadow-lg transition cursor-pointer border"
                onClick={() => onSelectProduct?.(product)}
              >
                {/* Product Image */}
                <div className="relative">
                  <img
                    src={imageSrc}
                    alt={product.name}
                    className="w-full h-40 object-cover rounded-t-lg"
                    onError={(e) => (e.currentTarget.src = DEFAULT_IMAGE)}
                  />

                  {/* Wishlist Button */}
                  <button
                    className="absolute top-2 right-2 bg-white rounded-full p-1 shadow hover:bg-gray-200"
                    onClick={(e) => {
                      e.stopPropagation();
                      onToggleWishlist?.(product);
                    }}
                  >
                    <Heart
                      size={18}
                      fill={isInWishlist ? "red" : "white"}
                      stroke={isInWishlist ? "red" : "black"}
                    />
                  </button>
                </div>

                {/* Product Info */}
                <div className="p-3">
                  <p className="font-semibold text-gray-900 text-sm truncate">
                    {product.name}
                  </p>

                  <p className="text-xs text-gray-600">
                    <span className="font-semibold">Brand:</span> {product.brand || ""}
                  </p>

                  <p className="text-xs text-gray-600">
                    <span className="font-semibold">Ref No:</span> {product.ref_no || ""}
                  </p>

                  <p className="text-xs text-gray-600">
                    <span className="font-semibold">Seller:</span> {product.seller}
                  </p>

                  {/* Price */}
                  {price > 0 ? (
                    <div className="mt-2">
                      {discount > 0 ? (
                        <div className="flex items-center gap-2">
                          <span className="text-gray-400 line-through text-xs">
                            {price.toFixed(2)}
                          </span>
                          <span className="text-green-600 font-semibold text-sm">
                            {finalPrice.toFixed(2)}
                          </span>
                          <span className="bg-green-100 text-green-700 text-xs px-1.5 py-0.5 rounded">
                            {discount}% OFF
                          </span>
                        </div>
                      ) : (
                        <span className="text-gray-900 font-semibold text-sm">
                          {price.toFixed(2)}
                        </span>
                      )}
                    </div>
                  ) : (
                    <p className="text-red-500 text-xs mt-1">Price unavailable</p>
                  )}
                </div>
              </div>
            );
          })
        )}
      </div>
    </div>
  );
};

export default ProductsList;

