import React from "react";
import { CartItem, ProductVariant } from "../../types";


const DEFAULT_IMAGE = "/images/default_product.png";


type Props = {
  products: CartItem[];
  onClose: () => void;
  onUpdateQuantity?: (
    cartItemId: number,
    productId: number,
    newQty: number,
    variantId?: number | null
  ) => void;
  onCheckout?: () => void;
  onClearItem?: (cartItemId: number) => void;
};

export default function CartPanel({
  products,
  onUpdateQuantity,
  onCheckout,
  onClearItem,
  onClose,
}: Props) {
  // =============================
  // PROCESS PRODUCTS WITH GST + DISCOUNT
  // =============================
  const safeProducts = products.map((p) => {
    const product = p.product;
    const image = product.image?.startsWith("http")
      ? product.image
      : DEFAULT_IMAGE;

    const variant: ProductVariant | null = p.variant || null;

    const basePrice = Number(
      variant?.price ??
        variant?.est_price ??
        (product as any).price ??
        0
    );

    const discount = Number(product.discount ?? 0);
    const gst = Number(product.gst ?? 0);

    // 1. Apply discount
    const discountedPrice = basePrice - (basePrice * discount) / 100;

    // 2. Calculate GST
    const gstAmount = (discountedPrice * gst) / 100;

    // 3. Final Price
    const finalPrice = discountedPrice + gstAmount;

    // 4. Savings from discount
    const savings = basePrice - discountedPrice;

    return {
      id: p.id,
      product,
      variant,
      image,
      basePrice,
      discountedPrice,
      gstAmount,
      finalPrice,
      savings,
      variantLabel: variant?.variant_label ?? "",
      quantity: p.quantity,
    };
  });

  // =============================
  // TOTALS SECTION
  // =============================
  const subtotal = safeProducts.reduce(
    (sum, i) => sum + i.discountedPrice * i.quantity,
    0
  );

  const gstTotal = safeProducts.reduce(
    (sum, i) => sum + i.gstAmount * i.quantity,
    0
  );

  const savingsTotal = safeProducts.reduce(
    (sum, i) => sum + i.savings * i.quantity,
    0
  );

  

  const grandTotal = subtotal + gstTotal ;

  // =============================
  // RENDER UI
  // =============================
  return (
    <div className="w-full max-w-6xl mx-auto p-6 bg-white rounded-lg shadow-md">
      {/* HEADER */}
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-semibold">Cart</h1>
        <button
          onClick={onClose}
          className="text-gray-600 hover:text-black text-xl"
        >
          
        </button>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* =======================
            LEFT SIDE - CART ITEMS
        ======================== */}
        <div className="lg:col-span-2 space-y-5">
          {safeProducts.length === 0 ? (
            <p className="text-gray-600 text-lg text-center py-10">
              Your cart is empty
            </p>
          ) : (
            safeProducts.map((item) => (
              <div
                key={item.id}
                className="flex gap-4 border p-4 rounded-lg shadow-sm"
              >
                {/* IMAGE */}
                <img
                  src={item.image || DEFAULT_IMAGE}
                  alt={item.product.name}
                  className="w-28 h-28 object-cover rounded"
                  onError={(e) =>
                    ((e.target as HTMLImageElement).src = DEFAULT_IMAGE)
                  }
                />

                {/* PRODUCT DETAILS */}
                <div className="flex flex-col flex-1">
                  <h2 className="font-semibold text-gray-800 text-lg">
                    {item.product.name}
                  </h2>

                  <p className="text-gray-600 text-sm">
                    Seller:{" "}
                    <span className="text-gray-700">
                      {item.product.seller}
                    </span>
                  </p>

                  <p className="text-gray-600 text-sm">
                    Variant: {item.variantLabel || "Default"}
                  </p>

                  {/* PRICE DISPLAY */}
                  <div className="flex flex-col mt-1">
                    <span className="text-xl font-semibold text-green-600">
                      {item.basePrice.toFixed(2)}
                    </span>

                    <span className="text-sm text-gray-500">
                      Price after discount: {item.discountedPrice.toFixed(2)}
                    </span>
                    <span className="text-sm text-gray-500">
                      GST ({item.product.gst}%): {item.gstAmount.toFixed(2)}
                    </span>

                   


                    
                  </div>

                  {/* ACTIONS */}
                  <div className="flex justify-between items-center mt-3">
                    {/* QUANTITY */}
                    {onUpdateQuantity && (
                      <div className="flex items-center border rounded">
                        <button
                          className="px-3 py-1 border-r text-lg"
                          disabled={item.quantity <= 1}
                          onClick={() =>
                            onUpdateQuantity(
                              item.id,
                              item.product.id,
                              item.quantity - 1,
                              item.variant?.id ?? null
                            )
                          }
                        >
                          
                        </button>

                        <span className="px-4">{item.quantity}</span>

                        <button
                          className="px-3 py-1 border-l text-lg"
                          onClick={() =>
                            onUpdateQuantity(
                              item.id,
                              item.product.id,
                              item.quantity + 1,
                              item.variant?.id ?? null
                            )
                          }
                        >
                          +
                        </button>
                      </div>
                    )}

                    {/* REMOVE ITEM */}
                    <div className="flex flex-col text-sm text-blue-600">
                      <button className="hover:underline">Save for later</button>

                      {onClearItem && (
                        <button
                          onClick={() => onClearItem(item.id)}
                          className="text-red-500 hover:underline mt-1"
                        >
                          Remove
                        </button>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            ))
          )}
        </div>

        {/* =======================
            RIGHT SIDE - SUMMARY
        ======================== */}
        <div className="p-5 border rounded-lg shadow-sm">
          <h3 className="text-xl font-semibold mb-4">Order Summary</h3>

          <div className="flex justify-between mb-2">
            <span>Subtotal (after discount)</span>
            <span>{subtotal.toFixed(2)}</span>
          </div>

          <div className="flex justify-between mb-2">
            <span>GST Total</span>
            <span>{gstTotal.toFixed(2)}</span>
          </div>

          <div className="flex justify-between mb-2 text-green-600">
            <span>Your Savings</span>
            <span> {savingsTotal.toFixed(2)}</span>
          </div>


          <hr className="my-3" />

          <div className="flex justify-between text-lg font-bold mb-4">
            <span>Total</span>
            <span>{grandTotal.toFixed(2)}</span>
          </div>

          <button
            onClick={onCheckout}
            className="w-full bg-blue-600 text-white py-3 rounded-lg hover:bg-blue-700"
          >
            Go to Checkout
          </button>

          
          
        </div>
      </div>
    </div>
  );
}
